.top_nav_wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.top_nav {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 20px 10px 10px 10px;
    width: fit-content;
}

.recent_list_title {
    margin: 5px 0;
}

.recent_list_wrapper_wrapper {
    position: relative;
    width: 100%;
}


/* used https://stackoverflow.com/questions/22666063/how-to-fade-the-edge-of-a-div-with-just-css */
.recent_list_wrapper_wrapper:before {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 0;
    pointer-events: none;
    background-image: linear-gradient(to left, rgba(var(--background-rgb),0), rgba(var(--background-rgb), 1) 90%);
    width: 40px;
    height: 100%;
}

.recent_list_wrapper_wrapper:after {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: 0;
    pointer-events: none;
    background-image: linear-gradient(to right, rgba(var(--background-rgb),0), rgba(var(--background-rgb), 1) 90%);
    width: 40px;
    height: 100%;
}

.recent_list_wrapper {
    overflow: auto hidden;
    width: 100%;
    /* background:red; */
    padding: clamp(10px, 1.5vh, 15px) 5px;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    /* box-shadow: inset 80px 0 40px -40px red; */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.recent_list_wrapper::-webkit-scrollbar {
    display: none;
}

.recent_list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
    /* width: 100%; */
    padding: 0 40px;
    min-width: calc(100% - 80px)s;
    width: fit-content;
    /* background:red; */
}

