.text_input_row {
    display: flex;
    flex-direction: row;
    width: calc(100% - 20px);
    height: 30px;
    gap: 10px;
    margin: 10px;
}

.text_input_row :first-child {
    flex: 1;
    font-size: 125%;
    border: solid 1px white;
    background: #666666;
    color: white;
    border-radius: 3px;
}

.text_input_row :first-child:disabled {
    color: #999999;
    border-color: #999999;
}

.angle_thumbnail_list {
    display: flex;
    flex-direction: row;
    justify-content: stretch;
    align-items: flex-start;
    gap: 10px;
    flex-wrap: wrap;
    padding: 0 10px;
}

.angle_thumbnail_area {
    background-color: #666666;
    /* border: 1px solid #808080; */
    border-radius: 5px;
    min-width: clamp(50px, 15vh, 100px);
    min-height: clamp(100px, 30vh, 200px);
    /* width: fit-content; */
    /* text-align: center; */
    flex-grow: 1;
    transition: transform .2s; /* Animation */
}

.art_thumbnail_area {
    background-color: #cccccc;
    /* border: 1px solid #808080; */
    border-radius: 5px;
    /* width: fit-content; */
    /* text-align: center; */
    /* flex-grow: 1; */
    transition: transform .2s; /* Animation */
    overflow: hidden;
    width: clamp(200px, 30vh, 300px);
    height: clamp(200px, 30vh, 300px);
    display: flex;
    flex-direction: column;
    gap: 3px;
    position: relative;
}

.art_thumbnail_image_row {
    overflow: hidden;
    display: flex;
    flex-direction: row;
    height: 50%;
    gap: 3px;
}

.art_thumbnail_image_row > div {
    overflow: hidden;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #666666;
}

.art_thumbnail_image_row > div > img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.art_thumbnail_num_leds {
    color: #cccccc;
    font-weight: bold;
}

.art_thumbnail_num_leds_number {
    font-size: clamp(50px, 7.5vh, 75px);
    line-height: 100%;
}

.angle_thumbnail_area:hover, .angle_thumbnail_area:active, .art_thumbnail_area:hover, .art_thumbnail_area:active {
    transform: scale(1.05);
    box-shadow: 0 0 6px 0 black;
}

.angle_thumbnail_label {
    background-color: #1976d2;
    padding: 5px;
    border-radius: 5px;
    position: absolute;
    /* display: inline; */
    /* left: 10px;
    top: 10px; */
    margin: 10px;
    width: fit-content;
    box-shadow: 0 0 6px -1px black;
}

.art_thumbnail_label_wrapper {
    position: absolute;
    width: clamp(200px, 30vh, 300px);
    height: clamp(200px, 30vh, 300px);
    /* background: red; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.art_thumbnail_label {
    color: #1976d2;
    background-color: white;
    font-size: 140%;
    font-weight: bold;
    padding: 5px;
    border-radius: 5px;
    /* display: inline; */
    /* left: 10px;
