.overlay {
    display: none;
}

.full_window .overlay {
    display: block;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    color: white;
    display: flex;
    justify-content:center;
    align-items: center;
    pointer-events: none;
}

.overlay > * {
    pointer-events: auto;
}

.overlay_top_row {
    position: fixed;
    width: 100%;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    pointer-events: none;
}

.overlay_top_row > * {
    pointer-events: auto;
}

.plot_overlay_top {
    position: fixed;
    width: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    pointer-events: none;
}

.plot_menu_bar {
    pointer-events: auto;
    background-color: rgba(255, 255, 255, 0.8);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0.25in 10px 0 10px;
    padding: 5px;
    border-radius: 10px;
    max-width: calc(100% - 20px);
}

@media screen and (max-width: 600px) {
    .plot_menu_bar {
        flex-direction: column;
        max-width: fit-content;
    }
}

.plot_below_menu {
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}

.plot_below_menu > * {
